import React from 'react';
import { db } from '../../config/firebaseConfig'

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from '@material-ui/core/MenuItem';
// core components
import GridItem from "../../components/Grid/GridItem";
import GridContainer from "../../components/Grid/GridContainer";
import CustomInput from "../../components/CustomInput/CustomInput";
import Button from "../../components/CustomButtons/Button";
import Card from "../../components/Card/Card.js";
import CardHeader from "../../components/Card/CardHeader.js";
import CardBody from "../../components/Card/CardBody.js";
import CardFooter from "../../components/Card/CardFooter.js";
import CustomSelect from '../../components/CustomSelect/CustomSelect'



const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);

const RegisterUser = () => {
  const classes = useStyles();
  const [ sex, setSex ] = React.useState('');
  const [ grade, setGrade ] = React.useState('');
  const [ inputs , setInputs ] = React.useState({
    name: "",
    evangelizer: "",
    phoneNumber: "",
    birthday: "",
    address: "",
    introduce: "",
  });
  
  const onInputChange = (e) => {
    const {name, value } = e.target
    setInputs({
      ...inputs,
      [name] : value
    })
  }

  const handleSexChange = (event) => {
    setSex(event.target.value);
  };

  const handleGradeChange = (event) => {
    setGrade(event.target.value);
  };

  const onSubmit = () => {
    alert('    .')
  }
  

  return (
    <div>
      <GridContainer >
        <GridItem xs={12} sm={12} md={8}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}></h4>
              {/* <p className={classes.cardCategoryWhite}>Complete your profile</p> */}
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput 
                    labelText="" 
                    id="name" 
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      onChange: onInputChange,
                      name: "name",
                      value: inputs.name
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText=""
                    id="evangelizer"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      onChange: onInputChange,
                      name: "evangelizer",
                      value: inputs.evangelizer
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText=""
                    id="phoneNumber"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      placeholder: '010-1234-5678',
                      onChange: onInputChange,
                      name: "phoneNumber",
                      value: inputs.phonenumber
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText=" "
                    id="birthday"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: '2000-01-01',
                      onChange: onInputChange,
                      name: "birthday",
                      value: inputs.birthday
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <CustomInput
                    labelText=""
                    id="address"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      onChange: onInputChange,
                      name: "address",
                      value: inputs.address
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomSelect
                    labelText=""
                    id="sex"
                    formControlProps={{
                      fullWidth: true
                    }}
                    selectProps={{
                      value: sex,
                      onChange: handleSexChange
                    }}
                  >
                    <MenuItem value={'male'}></MenuItem>
                    <MenuItem value={'female'}></MenuItem>
                  </CustomSelect>
                </GridItem>
                <GridItem xs={12} sm={12} md={8}>
                <CustomSelect
                    labelText=""
                    id="grade"
                    formControlProps={{
                      fullWidth: true
                    }}
                    selectProps={{
                      value: grade,
                      onChange: handleGradeChange
                    }}
                  >
                    <MenuItem value={'M1'}> 1</MenuItem>
                    <MenuItem value={'M2'}> 2</MenuItem>
                    <MenuItem value={'M3'}> 3</MenuItem>
                    <MenuItem value={'H1'}> 1</MenuItem>
                    <MenuItem value={'H2'}> 2</MenuItem>
                    <MenuItem value={'H3'}> 3</MenuItem>
                  </CustomSelect>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <InputLabel style={{ color: "#AAAAAA", marginTop:20}}> </InputLabel>
                  <CustomInput
                    labelText="    "
                    id="aboutMe"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      multiline: true,
                      rows: 5,
                      onChange: onInputChange,
                      name: "introduce",
                      value: inputs.introduce
                    }}
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
            <CardFooter>
              <Button onClick={()=>onSubmit()} color="primary"> </Button>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}

export default RegisterUser;